<template>
  <div class="sidebar" background-color="green">
    <!--  -->
    <el-menu
      class="sidebar-el-menu"
      background-color="#324157"
      :collapse="width > 600 ? collapse : false"
      text-color="#bfcbd9"
      mode="horizontal"
      active-text-color="#20a0ff"
      :default-active="defaultActive"
      unique-opened
      router
    >
      <template v-for="item in items" :key="item.index">
        <template v-if="item.subs && item.subs.length > 0">
          <el-sub-menu :index="item.index + ''">
            <template #title>
              <i :class="item.icon"></i>
              <span>{{ item.title }}</span>
            </template>
            <template v-for="subItem in item.subs" :key="subItem.index">
              <template v-if="subItem.subs && subItem.subs.length > 0">
                <el-sub-menu :index="subItem.index">
                  <template #title>{{ subItem.title }}</template>
                  <el-menu-item
                    v-for="(threeItem, i) in subItem.subs"
                    :key="i"
                    :index="threeItem.index"
                    >{{ threeItem.title }}</el-menu-item
                  >
                </el-sub-menu>
              </template>
              <template v-else>
                <el-menu-item :index="subItem.index">{{
                  subItem.title
                }}</el-menu-item>
              </template>
            </template>
          </el-sub-menu>
        </template>
        <template v-else>
          <el-menu-item :index="item.index" :key="item.index">
            <i :class="item.icon"></i>
            <template #title>{{ item.title }}</template>
          </el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "SideBar",
  data() {
    return {
      items: [
        {
          icon: "el-icon-s-home",
          index: "/",
          title: "首页",
          // subs: [],
        },
        {
          icon: "el-icon-camera",
          index: "/album",
          title: "图像记忆",
          // subs: [],
        },
        {
          icon: "el-icon-edit",
          index: "/record",
          title: "点滴生活",
          // subs: [],
        },
      ],
      defaultActive: "",
      width: document.body.clientWidth,
    };
  },
  computed: {
    ...mapState("app", ["collapse"]),
  },
  mounted() {
    this.defaultActive = this.$route.path;
    // console.dir(this.collapse);
  },
  watch: {
    $route(newValue) {
      this.defaultActive = newValue.path;
    },
  },
};
</script>

<style scoped>
@media (max-width: 600px) {
  .sidebar {
    /* background: blueviolet;
  float: left;
  bottom: 0px;
  height: 100%;
  overflow-y: scroll;
  overflow: hidden; */
    width: 100%;
    display: block;
    position: absolute;
    bottom: 0;
    overflow-x: scroll;
  }
  .sidebar-el-menu:not(.el-menu--collapse) {
    width: 100%;
    height: 50px;
  }
}
@media (min-width: 600px) {
  .sidebar {
    /* background: blueviolet;
  float: left;
  bottom: 0px;
  height: 100%;
  overflow-y: scroll;
  overflow: hidden; */

    display: block;
    position: absolute;
    top: 70px;
    bottom: 0;
    overflow-y: scroll;
  }
  .sidebar-el-menu:not(.el-menu--collapse) {
    width: 130px;
    height: 100%;
  }
}
.sidebar::-webkit-scrollbar {
  width: 0;
}

.sidebar > ul {
  height: 100%;
}
</style>
