<template>
  <div class="header">
    <div class="collapse-btn" @click="collapseAction">
      <i v-if="!collapse" class="el-icon-s-fold"></i>
      <i v-else class="el-icon-s-unfold"></i>
    </div>

    <div class="logo">My Life</div>
    <div class="header-right">
      <div class="header-user-con">
        <!-- 消息中心 -->
        <div class="btn-bell">
          <!--  提示 -->
          <el-tooltip
            effect="dark"
            :content="message ? `有${message}条未读消息` : `消息中心`"
            placement="bottom"
          >
            <router-link to="/">
              <i class="el-icon-bell"></i>
            </router-link>
          </el-tooltip>
          <span class="btn-bell-badge" v-if="message"></span>
        </div>
        <!-- 用户头像 -->
        <div class="user-avator">
          <img src="/imgs/img.jpg" />
        </div>
        <!-- 用户名下拉菜单 -->
        <el-dropdown class="user-name" trigger="click" @command="handleCommand">
          <span class="el-dropdown-link">
            {{ username }}
            <i class="el-icon-caret-bottom"></i>
          </span>
          <template #dropdown>
            <el-dropdown-menu>
              <a
                class="github"
                href="https://github.com/lin-xin/vue-manage-system"
                target="_blank"
              >
                <el-dropdown-item>项目仓库</el-dropdown-item>
              </a>
              <el-dropdown-item divided command="loginout"
                >退出登录</el-dropdown-item
              >
            </el-dropdown-menu>
          </template>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>

<script>
import { useStore, mapState, mapActions, mapMutations } from "vuex";
export default {
  name: "Header",
  components: {},
  data() {
    return {
      message: 3,
      name: "未登录",
    };
  },
  computed: {
    username: {
      get() {
        var username = localStorage.getItem("ms_username");
        // console.dir(localStorage);
        return username ? username : this.name;
      },
    },
    //   // ...mapState(["collapse"]),
    //   ...mapState({
    //     // 传字符串参数 'count' 等同于 `state => state.count`
    //     collapse: "collapse",
    //   }),
    //   // collapse: {
    //   //   get() {
    //   //     return this.$store.state.collapse;
    //   //   },
    //   //   set(value) {
    //   //     this.$store.state.collapse = value;
    //   //   },
    //   // },
    ...mapState("app", {
      // 传字符串参数 'count' 等同于 `state => state.count`
      collapse: "collapse",
      count: "count",
    }),
  },
  methods: {
    ...mapActions("app", {
      increm: "increment",
      collapseAction: "collapse",
    }),
    ...mapMutations("app", {
      add: "increment", // 将 `this.add()` 映射为 `this.$store.commit('increment')`
    }),
    // 用户名下拉菜单选择事件
    handleCommand(command) {
      if (command == "loginout") {
        localStorage.removeItem("ms_username");
        this.$router.push("/login");
      }
    },
  },
  mounted() {},
};
</script>

<style scoped>
@media (min-width: 600px) {
  .header {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 70px;
    font-size: 22px;
    color: #fff;
    background: rgb(49, 36, 36);
  }
  .collapse-btn {
    float: left;
    padding: 0 21px;
    cursor: pointer;
    line-height: 70px;
  }
  .header .logo {
    float: left;
    width: 250px;
    line-height: 70px;
  }
}

@media (max-width: 600px) {
  .header {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 42px;
    font-size: 22px;
    color: #fff;
    background: rgb(49, 36, 36);
  }
  .collapse-btn {
    float: left;
    padding: 0 21px;
    cursor: pointer;
    line-height: 42px;
  }
  .header .logo {
    float: left;
    width: 250px;
    line-height: 42px;
  }
}

.header-right {
  float: right;
  padding-right: 50px;
}
.header-user-con {
  display: flex;
  height: 70px;
  align-items: center;
}
.btn-fullscreen {
  transform: rotate(45deg);
  margin-right: 5px;
  font-size: 24px;
}
.btn-bell,
.btn-fullscreen {
  position: relative;
  width: 30px;
  height: 30px;
  text-align: center;
  border-radius: 15px;
  cursor: pointer;
}
.btn-bell-badge {
  position: absolute;
  right: 0;
  top: -2px;
  width: 8px;
  height: 8px;
  border-radius: 4px;
  background: #f56c6c;
  color: #fff;
}
.btn-bell .el-icon-bell {
  color: #fff;
}
.user-name {
  margin-left: 10px;
}
.user-avator {
  margin-left: 20px;
  margin-right: 5px;
}
.user-avator img {
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.el-dropdown-link {
  color: #fff;
  cursor: pointer;
}
.el-dropdown-menu__item {
  text-align: center;
}
.github {
  text-decoration: none;
}
</style>
