<template>
  <div class="wrapper">
    <Header />
    <SideBar />
    <div :class="{ 'content-collapse': collapse }" class="content-box">
      <Tag />
      <router-view v-slot="{ Component }">
        <keep-alive :include="tagsList">
          <component :is="Component" />
        </keep-alive>
      </router-view>
    </div>
  </div>
</template>
<script>
import { mapState, mapActions, mapMutations } from "vuex";

import Header from "../components/Header_1.vue";
import SideBar from "../components/SideBar.vue";
import Tag from "../components/Tag.vue";
export default {
  name: "Index",
  components: { Header, SideBar, Tag },
  data() {
    return {
      width: document.body.clientWidth,
    };
  },
  computed: {
    ...mapState("app", ["collapse", "count", "tagsList"]),
  },
  method: {
    ...mapActions("app", {
      increment: "increment",
    }),
    ...mapMutations("app", {
      add: "increment", // 将 `this.add()` 映射为 `this.$store.commit('increment')`
    }),
  },

  mounted() {
    // console.dir(this.$router);
    // console.dir(this.$route);
    // console.dir(this.$store);
    // console.dir(this.count);
  },
};
</script>
<style>
.wrapper {
  margin: 0;
  height: 100%;
  padding: 0;
  background-color: #f8f9fa !important;
}
@media (min-width: 600px) {
  .content-box {
    position: absolute;
    left: 130px;
    right: 0;
    top: 70px;
    bottom: 0;
    padding-bottom: 0px;
    -webkit-transition: left 0.3s ease-in-out;
    transition: left 0.3s ease-in-out;
    background: #f0f0f0;
    overflow: hidden;
  }
  .content-collapse {
    left: 65px;
  }
}
@media (max-width: 600px) {
  .content-box {
    position: absolute;
    left: 0px;
    right: 0;
    top: 42px;
    bottom: 50px;
    -webkit-transition: left 0.3s ease-in-out;
    transition: left 0.3s ease-in-out;
    background: #f0f0f0;
    overflow: hidden;
  }
  .content-collapse {
    left: 0px;
  }
}
</style>
