<template>
  <div v-if="showTags" class="content">
    <div class="tags">
      <ul>
        <li
          v-for="(item, index) in tagsList"
          class="tags-li"
          :class="{ active: isActive(item.path) }"
          :key="index"
        >
          <router-link :to="item.path" class="tags-li-title"
            >{{ item.title }}
          </router-link>
          <span v-if="item.close" class="tags-li-icon" @click="closeTag(index)"
            ><i class="el-icon-close"></i
          ></span>
        </li>
      </ul>
    </div>
    <div class="tags-close-box" size="">
      <el-dropdown @command="handleTag">
        <el-button size="mini" type="primary">
          <i class="el-icon-arrow-down el-icon--right"> 标签选项 </i>
        </el-button>
        <template #dropdown>
          <el-dropdown-menu size="small">
            <el-dropdown-item command="other">关闭其他</el-dropdown-item>
            <el-dropdown-item command="all">关闭所有</el-dropdown-item>
          </el-dropdown-menu>
        </template>
      </el-dropdown>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
export default {
  name: "Tag",
  data() {
    return {};
  },
  computed: {
    ...mapState("app", ["tagsList", "count"]),
  },
  methods: {
    ...mapMutations("app", {
      test: "increment", // 将 `this.add()` 映射为 `this.$store.commit('increment')`
      delTag: "delTagsItem",
      setTag: "setTagsItem",
      closeAllTag: "clearTags",
      closeOtherTag: "closeTagsOther",
    }),
    isActive(path) {
      return path === this.$route.fullPath;
    },
    showTags() {
      return this.tagsList.length > 0;
    },
    closeTag(index) {
      console.dir(index);
      this.delTag(index);
      const item =
        this.tagsList.length > 0
          ? this.tagsList[this.tagsList.length - 1]
          : null;
      if (item) {
        this.$router.push(item.path);
      } else {
        this.$router.push("/");
      }
    },
    setTags(route) {
      const isExist = this.tagsList.some((item) => {
        return item.path === route.fullPath;
      });
      if (!isExist) {
        if (this.tagsList.length >= 8) {
          this.delTag(0);
        }
        if (route.path != "/") {
          this.setTag(route);
        }
      }
    },
    handleTag(command) {
      command === "other" ? this.closeOther() : this.closeAll();
    },
    // 关闭全部标签
    closeAll() {
      this.closeAllTag();
      this.$router.push("/");
    },
    // 关闭其他标签
    closeOther() {
      const curItem = this.tagsList.filter((item) => {
        return item.path === this.$route.fullPath;
      });
      this.closeOtherTag(curItem);
    },
  },
  watch: {
    $route(newValue) {
      this.setTags(newValue);
    },
  },
  created() {
    this.setTags(this.$route);
  },
};
</script>

<style scoped>
.tags {
  left: 0px;
  height: 33px;
  padding-right: 110px;
  padding-left: 10px;
  padding-bottom: 5px;
  overflow: hidden;
  background: #fff;
  /* box-shadow: 0 5px 10px #ddd; */
}

.tags ul {
  box-sizing: border-box;
  width: 100%;
  overflow-x: auto;
  height: 100%;
  padding: 0;
  margin: 0;
  white-space: nowrap;
}

.tags-li {
  display: inline-block;
  margin: 3px 5px 2px 3px;
  border-radius: 3px;
  font-size: 12px;
  overflow: hidden;
  cursor: pointer;
  height: 23px;
  line-height: 23px;
  border: 1px solid #e9eaec;
  background: #fff;
  padding: 0 8px 0 8px;
  vertical-align: middle;
  color: #666;
  -webkit-transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
}

/* .tags-li:not(.active):hover {
  background: #f8f8f8;
}

.tags-li.active {
  color: #fff;
} */

.tags-li-title {
  float: left;
  max-width: 80px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #666;
}

.tags-li.active .tags-li-title {
  color: rgb(119, 24, 24);
}

.tags-close-box {
  position: absolute;
  float: right;
  right: 0;
  top: 0;
  box-sizing: border-box;
  padding-top: 1px;
  text-align: center;
  width: 110px;
  height: 30px;
  background: #fff;
  box-shadow: -3px 0 15px 3px rgba(0, 0, 0, 0.1);
  z-index: 10;
}
</style>
